@charset "UTF-8";

//----------------------------------------------
.navbar-default {
  margin-bottom: 0;
  border-bottom: 0;
  background-color: transparent;

  .navbar-brand {
    margin-top: 0;
    margin-bottom: 0;
  }

  .navbar-nav {
    @media (min-width: $screen-sm-min) {
      margin-top: 75px;
    }

    &:not(.contact) {
      li {
        + li {
          a {
            position: relative;

            &:before {
              @media (min-width: $screen-sm-min) {
                position: absolute;
                display: block;
                top: 12px;
                left: -3px;
                font-size: 20px;
                font-weight: bold;
                content: ":";
              }
            }

            &:hover,
            &:focus {
              &:before {
                color: #361E58;
              }
            }
          }
        }
      }
    }

    > li {
      > a {
        font-size: 20px;
        font-weight: bold;
        color: #361E58;

        @media (min-width: $screen-sm-min) and (max-width: $screen-md-max) {
          font-size: 18px;
        }

        &:not(.btn) {
          &:hover,
          &:after {
            @include opacity (.7);
            color: red;
          }
        }
      }
    }
  }

  .navbar-right {
    @media (min-width: $screen-sm-min) {
      margin-right: 0;
    }
  }

  .contact {
    @media (max-width: $screen-sm-max) {
      margin-bottom: 16px;
      text-align: center;
    }

    li {
      @media (max-width: $screen-sm-max) {
        display: inline-block;
      }
      + li {
        padding-left: 20px;
      }
    }

    .btn {
      padding: 8px 0;
      width: 44px;
      height: 44px;
      border-radius: 50%;

      &:hover,
      &:focus {
        @include opacity (.7);
        background-color: #361E58;
      }
    }
  }
}

//----------------------------------------------
.navbar-collapse {
  @media (max-width: $grid-float-breakpoint-max) {
    position: absolute;
    top: 130px;
    z-index: 20;
    width: 100%;
    background-color: #ffcb08;
    border-top: 0;
  }
}

//----------------------------------------------
.navbar-toggle {
  margin-top: 20px;
}

//----------------------------------------------
.menu-trigger {
  position: absolute;
  top: 3px;
  right: 0;
  width: 40px;
  height: 32px;
  transition: all .3s;

  @media (max-width: $screen-xs-max) {
    display: inline-block;
  }

  &,
  &:hover,
  &:focus,
  &:active {
    background-color: transparent !important;
    border: 0;
    outline: 0;
  }

  .icon-bar {
    position: absolute;
    left: 0;
    display: inline-block;
    width: 100%;
    height: 4px;
    background-color: #361E58 !important;
    border-radius: 4px;
    transition: all .3s;

    &:nth-of-type(1) {
      top: 0;
    }

    &:nth-of-type(2) {
      top: 14px;
    }

    &:nth-of-type(3) {
      bottom: 0;
    }

    & + .icon-bar {
      margin-top: 0;
    }
  }

  &.active {
    .icon-bar {
      &:nth-of-type(1) {
        transform: translateY(14px) rotate(-45deg);
      }

      &:nth-of-type(2) {
        opacity: 0;
      }

      &:nth-of-type(3) {
        transform: translateY(-14px) rotate(45deg);
      }
    }
  }
}

//----------------------------------------------
.nav-fixed-bottom {
  position: fixed;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1030;
  padding-top: 20px;
  background-color: #ffcb08;

  .table-cell {
    background-color: #361E58;
  }

  .list-inline {
    margin-bottom: 20px;
    text-align: center;

    .btn {
      padding: 8px 0;
      width: 44px;
      height: 44px;
    }
  }

  .cell {
    width: 33.3333333333%;
    padding: 10px 5px;
    vertical-align: middle;
    font-weight: bold;
    font-size: 13px;
    color: #ffcb08;
    text-align: center;

    @media (max-width: $screen-xxs-max) {
      &.website {
        width: 23%;
      }

      &.print {
        width: 39%;
      }
    }

    + .cell {
      position: relative;

      &:before {
        position: absolute;
        display: block;
        top: 8px;
        left: -2px;
        font-size: 14px;
        font-weight: bold;
        content: ":";
      }
    }
  }
}
