@charset "UTF-8";

//----------------------------------------------
.section-404 {
  padding-top: 100px;

  @media (max-width: $screen-sm-max) {
    padding-top: 20px;
    padding-bottom: 130px;
  }

  .title-section {
    @include center-block ();
    max-width: 600px;
    padding: 15px 20px;
    margin-top: 35px;
    margin-bottom: 0;
    border: 2px solid white;
    border-radius: 5px;

    @media (max-width: $screen-sm-max) {
      padding: 10px 5px;
    }
  }

  .logo-white-xs {
    margin-bottom: 30px;
  }

  .img {
    width: 500px;
    animation-name: img-404-floating, fadein;
    animation-duration: 5s;
    animation-iteration-count: infinite;
    animation-timing-function: ease-in-out;

    @keyframes img-404-floating {
      from { transform: translate(0,  0px); }
      65%  { transform: translate(0, 15px); }
      to   { transform: translate(0, -0px); }
    }
    @keyframes fadein{
      0% { opacity:0; }
      70% { opacity:1; }
      100% { opacity:0; }
    }
  }
}
